.header {
	background-color: $primary_bg;
	padding: 30px 0 0;
	box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.1);
	position: fixed;
	width: 100%;
	top: 0;
	z-index: 6;
	@include trans;
	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 25px 0;
		margin: 0 0 38px;
		@include trans;
		.menu-burger {
			margin: 0 0 0 30px;
		}
	}
	.search-form {
		margin: 0 auto 0 3.5rem;
	}
	&__bottom {
		border-top: 2px solid $bdr;
		nav {
			border-left: 2px solid $bdr;
			border-right: 2px solid $bdr;
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		.menu {
			&__itm {
				&:nth-child(2) {
					.menu__lnk {
						padding-left: 34px;
					}
				}
			}
			&__lnk {
				padding: 21px 16px;
			}
		}
		.dropdown {
			&__set {
				.dropdown {
					border-left: 2px solid $bdr;
				}
			}
			&__btn {
				height: 100%;
				padding: 15px 27px;
				font-size: 14px;
				color: $text_sec;
				&:hover {
					color: $primary;
				}
				i {
					margin-left: 10px;
				}
			}
		}
	}
	&.scrolling {
		padding: 15px 0 0;
		.header {
			&__top {
				margin: 0 0 15px;
				padding: 0;
				.menu-burger {
					display: block;
				}
			}
			&__bottom {
				display: none;
			}
		}
	}
	&.static {
		.header {
			&__bottom {
				display: block;
			}
		}
	}
}

.logo {
	font-weight: 900;
	text-transform: uppercase;
	font-size: 22px;
	color: $text_dark;
	letter-spacing: 1.7px;
	text-decoration: none;
}



.search-form {
	display: flex;
	border: 2px solid $bdr;
	border-radius: $radius;
	.text-input {
		height: 47px;
		border-right: 2px solid $bdr;
		width: 276px;
	}
	&__btn {
		background-image: url("../img/svg/magnifier.svg");
		background-size: 14px;
		background-repeat: no-repeat;
		background-position: center;
		border: none;
		background-color: transparent;
		border-left: 2px solid $bdr;
		width: 50px;
		@include trans;
		&:hover {
			background-color: #FAFAFA;
		}
	}
	.dropdown {
		&__btn {
			height: 47px;
			padding: 16px;
			color: $text_sec;
			font-size: 10px;
			text-transform: uppercase;
			font-weight: 900;
			letter-spacing: 1.2px;
			min-width: 140px;
			justify-content: center;
			@include trans;
			i {
				@include trans;
			}
			&.active {
				color: $primary;
				i {
					background-image: url("../img/svg/chevron-down-hover.svg");
				}
			}
		}
	}
	.chevron-down-ic {
		margin-left: 13px;
	}
}

.header-actions {
	display: flex;
	&__itm {
		list-style: none;
		margin: 0 17px;
		&.active {
			.header-actions {
				&__icon {
					position: relative;
					&:before {
						content: "";
						position: absolute;
						right: -9px;
						top: -8px;
						border-radius: 50px;
						width: 6px;
						height: 6px;
						background-color: $primary;
					}
				}
			}
		}
	}
	&__lnk {
		display: inline-block;
		white-space: nowrap;
		text-decoration: none;
		font-size: 14px;
		@include links ($lnk, $text_dark, $primary, $text_dark, $primary);
		&:hover {
			.header-actions {
				&__icon {
					&.cart-ic {
						background-image: url("../img/svg/cart-hover.svg");
					}
					&.heart-ic {
						background-image: url("../img/svg/heart-hover.svg");
					}
					&.profile-ic {
						background-image: url("../img/svg/profile-hover.svg");
					}
				}
			}
		}
		span {
			display: inline-block;
		}
	}
	&__icon {
		margin: -2px 20px 0 0;
		@include trans;
	}
}

.menu {
	&__itm {
		list-style: none;
		display: inline-block;
		&.all-categories {
			position: relative;
			border-right: 2px solid $bdr;
			cursor: pointer;
			.menu {
				&__lnk {
					padding-left: 70px;
					padding-right: 40px;
				}
			}
		}
	    &.active {
	    	.menu {
	    		&__itm {
					&__burger {
						.mMt.one {
						    top: -50px;
							opacity: 0;
							background-color: $primary;
						}
						.mMt.two {
						    transform: rotate(45deg);
							top: 12px;
							background-color: $primary;
						}
						.mMt.three {
						    transform: rotate(-45deg);
							top: 12px;
							background-color: $primary;
						}
			    	}
		    	}
		    	&__lnk {
		    		color: $primary;
		    	}
	    	}
	    }
	    // &:not(.active) {
	    // 	.menu{
	    // 		&__lnk {
	    // 			color: $lnk;
	    // 		}
	    // 	}
	    // }
		&__burger {
			display: block;
			position: absolute;
		    width: 27px;
		    height: 30px;
		    overflow: hidden;
		    background: transparent;
		    top: 15px;
		    left: 25px;
		    pointer-events: none;
		    .mMt{
			    width: 17px;
			    height: 2px;
			    background: $lnk;
			    display: block;
			    position: absolute;
			    transition: all .3s ease-in-out;
			    left: 5px;
			}
			.mMt.one {
			    top: 7px;
			}
			.mMt.two {
			    top: 12px;
			}
			.mMt.three {
			    top: 17px;
			}
		}
	}
	&__lnk {
		text-decoration: none;
		@include links ($lnk, $text_dark, $primary, $text_dark, $primary);
		display: inline-block;
		font-size: 14px;
	}
}













@media only screen and (max-width: 1200px) {
	.header{
		&__bottom {
			.menu {
				&__lnk {
					padding: 20px 12px;
				}
			}
		}
	}
	.header-actions {
		&__lnk {
			span {
				display: none;
			}
		}
		&__itm {
			margin: 0 5px;
		}
	}
	.menu {
		&__itm {
			&.all-categories {
				.menu {
					&__lnk {
						padding-right: 15px;
						padding-left: 45px;
					}
				}
			}
			&__burger {
				left: 8px;
			}
		}
	}
}

@media only screen and (max-width: 991px) {
	.logo {
		font-size: 18px;
	}
	.header {
		&__top {
			padding: 0 0 25px;
			margin: 0;
		}
		.search-form {
			margin: 0 15px;
			.text-input {
				width: 175px;
			}
		}
		&__bottom {
			display: none;
			nav {
				flex-direction: column;
			}
			.dropdown {
				&__set {
					border-top: 2px solid $bdr;
					.dropdown {
						&:last-child {
							border-right: 2px solid $bdr;
						}
					}
				}
			}
		}
	}
	.menu {
		&__itm {
			&.all-categories {
				border-right: none;
			}
		}
	}
}

@media only screen and (max-width: 767px) {
	.header {
		&__top {
			flex-wrap: wrap;
		}
		&__bottom {
			.menu {
				display: flex;
				flex-direction: column;
				text-align: center;
				&__itm {
					&__burger {
						top: 10px;
					}
					&:nth-child(2) {
						.menu__lnk {
							padding-left: 0;
						}
					}
					&.all-categories {
						.menu__lnk {
							padding-left: 40px;
						}
					}
				}
				&__lnk {
					padding: 15px 0;
				}
			}
		}
		.search-form {
			order: 4;
			margin: 10px auto 0;
			@include trans;
			&.hide {
				height: 0px;
				opacity: 0;
				overflow: hidden;
				pointer-events: none;
			}
		}
		&.scrolling {
			.search-form {
				display: none;
			}
		}
	}
}

@media only screen and (max-width: 480px) {
	.header {
		&__top {
			.menu-burger {
				margin: 0;
			}
		}
		&__bottom {
			nav {
				border: none;
			}
		}
		.search-form {
			.text-input {
				width: 120px;
				font-size: 12px;
				padding: 10px;
			}
			.dropdown {
				&__btn {
					min-width: 120px;
					padding: 10px;
				}
			}
		}
	}
}


@media only screen and (min-width: 768px) {
	.header {
		&.static {
			.header__bottom {
				display: block !important;
			}
		}
	}
}


@media only screen and (min-width: 992px) {
	.header {
		&__top {
			.menu-burger {
				display: none;
			}
		}
	}
}






@media only screen and (min-width: 1025px) {
	.menu {
		&__itm {
			&.all-categories {
				> a.menu__lnk {
					//pointer-events: none;
				}
			}
		}
	}
}


