.footer {
	background-color: $primary_bg;
	&__top {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 35px 72px 40px;
		.menu {
			&__lnk {
				font-size: 16px;
			}
			&__itm {
				margin: 0 25px;
			}
			.dropdown {
				&__btn {
					&:hover {
						color: $primary;
					}
					i {
						margin: 0 0 0 10px;
					}
				}
				&__content {
					min-width: 140px;
					transform: translateX(-25%);
					top: 130%;
				}
			}
		}
		nav {
			margin-right: auto;
			margin-left: 140px;
		}
	}
	&__bottom {
		border-top: 2px solid $bdr;
		font-size: 14px;
		color: #b8b8b8;
		padding: 40px 0 50px;
		.menu {
			&__lnk {
				color: #b8b8b8;
				padding: 0 15px;
				&:not(span) {
					&:hover {
						color: $primary;
					}
				}
				
			}
		}
	}
	&__bottom-cont {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 70px;
	}
}

.social {
	&__itm {
		list-style: none;
		display: inline-block;
		margin: 0 4px;
	}
	&__lnk {
		padding: 10px;
		display: inline-block;
		&:hover {
			.facebook-logo-ic {
				background-image: url("../img/svg/facebook-logo-hover.svg");
			}
			.twitter-logo-ic {
				background-image: url("../img/svg/twitter-logo-hover.svg");
			}
			.instagram-logo-ic {
				background-image: url("../img/svg/instagram-logo-hover.svg");
			}
			.youtube-logo-ic {
				background-image: url("../img/svg/youtube-logo-hover.svg");
			}
		}
		i {
			@include trans;
		}
	}
}

.payment {
	&__wrap {
		display: flex;
		align-items: center;
	}
	&__name {
		display: inline-block;
		margin-right: 20px;
	}
	&__itm {
		list-style: none;
		display: inline-block;
		margin: 0 10px;
	}
}


@media only screen and (max-width: 1200px) {
	.footer {
		&__top {
			nav {
				margin-left: 30px;
			}
		}
	}
	.payment {
		&__itm {
			margin: 0 2px;
		}
	}
}

@media only screen and (max-width: 991px) {
	.footer {
		&__top {
			padding: 20px 0 10px;
			.menu {
				&__itm {
					margin: 0 10px;
				}
			}
		}
		&__bottom-cont {
			padding: 0;
			flex-direction: column;
			.payment {
				&__wrap {
					margin: 10px 0 0;
					flex-direction: column;
				}
				&__name {
					margin-bottom: 10px;
				}
			}
		}
	}
}

@media only screen and (max-width: 767px) {
	.footer {
		&__top {
			flex-direction: column;
			nav {
				margin: 0 auto 10px;
			}
			.logo {
				&__wrap {
					display: none;
				}
			}
		}
		&__bottom {
			padding: 10px 0 15px;
		}
	}
}

@media only screen and (max-width: 480px) {
	.footer {
		&__top {
			.menu {
				&__lnk {
				    font-size: 13px;
				}
				.dropdown__btn {
					font-size: 13px;
				}
			}
		}
		&__bottom-cont {
			.menu {
				display: none;
			}
		}
	}
}