h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

h1 {
	font-weight: 500;
	font-size: 30px;
}

* {
	box-sizing: border-box;
}

a {
	@include trans;
}

a, input, button {
	outline: none;
	cursor: pointer;
}



.container {
    @include container();
    @include sm(max-width, rem(540));
    @include md(max-width, rem(750));
    @include lg(max-width, rem(1054));
    @include xl(max-width, rem(1140));
}

body {
	background-color: #FAFAFA;
	font-family: 'Roboto', sans-serif;
	font-weight: 500;
	color: $text_pr;
	padding-top: 180px;
	&.modal-active {
		overflow: hidden;
	}
}


.btn {
	display: inline-block;
    background: 0 0;
    font-size: 14px;
    padding: 11px;
    width: 100%;
    max-width: 240px;
    text-align: center;
    @include trans;
    text-decoration: none;
    border-radius: $radius;
    text-shadow: none;
    min-width: 160px;
    box-shadow: none;
    outline: none;
    border: 2px solid $bdr;
    color: $text_pr;
    &-primary {
    	@extend .btn;
    	border-color: $primary;
    	color: $primary;
    	&:hover {
    		background-color: $primary;
    		border-color: $primary;
    		color: #fff;
    	}
    }
}


/* Dropdown Styles */
.dropdown {
	position: relative;
	&__set {
		display: flex;
	}
	&__content {
		position: absolute;
		display: none;
		width: 100%;
		border: 2px solid $bdr;
		background-color: #fff;
		padding: 15px;
		width: calc(100% + 4px);
		left: -2px;
		text-align: center;
		z-index: 1;
	}
	&.active {
		.dropdown__btn {
			color: $primary;
			i {
				transform: rotate(-180deg);
				background-image: url("../img/svg/chevron-down-hover.svg");
			}
		}
	}
	&__btn {
		cursor: pointer;
		user-select: none;
		display: flex;
		align-items: center;
		@include trans;
		i {
			@include trans;
		}
	}
	&__list {
		font-size: 14px;
		display: inline-block;
		li {
			list-style: none;
			a,
			span {
				display: inline-block;
				padding: 4px 0;
				color: $text_sec;
				text-decoration: none;
				cursor: pointer;
				&:hover {
					color: $primary;
				}
			}
		}
	}
}
/* END Dropdown Styles */

/*Custom checkbox*/
.cust-check {
	position: relative;
    margin: 0;
    padding: 0;
    display: inline-block;
    font-size: 12px;
    color: $text_pr;
    cursor: pointer;
    user-select: none;
    text-align: left;
    font-weight: 500;
    input {
    	position: absolute;
	    z-index: -10;
	    width: 0;
	    height: 0;
	    opacity: 0;
	    &:checked {
	    	~ .check-ic {
	    		background-color: $primary;
	    		border-color: $primary;
	    	}
	    }
    }
    .check-ic {
	    position: relative;
	    width: 20px;
	    height: 20px;
	    display: inline-block;
	    vertical-align: middle;
	    background-color: transparent;
	    border: 2px solid $bdr;
	    border-radius: $radius;
	    background-color: #fff;
	    background-size: cover;
	    background-position: center;
	    background-repeat: no-repeat;
	    margin: 0 20px 0 0;
	    @include trans;
    	span {
    		position: absolute;
		    top: 50%;
		    left: 50%;
		    transform: translate(-50%, -50%);
		    display: block;
		    height: calc(100% - 3px);
		    width: calc(100% - 3px);
		    border-radius: 10em;
		    border: 1px solid transparent;
		    background-image: url("../img/svg/check-white.svg");
		    background-size: cover;
		    background-repeat: no-repeat;
    	}
    }
}
/*END Custom checkbox*/

.img-inner-box {
    position: relative;
    display: block;
    text-decoration: none;
    border: none;
    width: 100%;
    .rectangle {
    	position: relative;
	    z-index: 1;
	    display: block;
	    width: 100% !important;
	    height: auto !important;
    }
    .main-img {
		object-fit: contain;
		position: absolute;
		display: block;
	    z-index: 2;
	    top: 0;
	    left: 50%;
	    transform: translateX(-50%);
	    height: 100% !important;
    }
}

.text-input {
	font-weight: 500;
	font-size: 14px;
	padding: 16px;
	border: none;
	&::-webkit-input-placeholder {color:$text_sec;}
    &::-moz-placeholder          {color:$text_sec; opacity: 1;}/* Firefox 19+ */
    &:-moz-placeholder           {color:$text_sec; opacity: 1;}/* Firefox 18- */
    &:-ms-input-placeholder      {color:$text_sec;}
}

.sale {
	text-transform: uppercase;
	background-color: #F7F7F7;
	display: inline-block;
	color: $text_sec;
	font-weight: 900;
	font-size: 10px;
	border-radius: $radius;
	padding: 6px 10px;
}

.page-modal {
	//display: none;
    position: fixed;
    z-index: -1;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
    pointer-events: none;
    @include trans;
    &.show {
		pointer-events: auto;
		z-index: 10;
		opacity: 1;
    }
	&__content {
		position: absolute;
		max-width: 500px;
		width: 100%;
		@include pageitem;
		padding: 20px;
		top: 50%;
		left: 50%;
		transform: translate(-50%);
		display: flex;
		flex-direction: column;
	}
	&__top {
		display: flex;
		justify-content: space-between;
	}
	&__middle {
		form {
			display: flex;
			flex-direction: column;
		}
		.cust-inp {
			margin: 20px 0 0;
			display: flex;
			flex-direction: column;
			&.has-error {
				.help-block {
					opacity: 1;
				}
			}
			input {
				border: 2px solid $bdr;
				border-radius: $radius;
				padding: 15px;
			}
			.help-block {
				display: inline-block;
				margin: 5px 0 0 5px;
				font-size: 12px;
				font-weight: 400;
				color: $primary;
				@include trans;
				opacity: 0;
			}
		}
	}
	.btn-wrap {
		margin: 20px 0;
	}
}

.close {
	cursor: pointer;
	&:hover {
		i {
			background-image: url("../img/svg/times-hover.svg");
		}
	}
	i {
		@include trans;
	}
}

.btn-wrap {
	text-align: center;
}

.menu-burger {
	display: block;
    position: relative;
    width: 36px;
    height: 36px;
    overflow: hidden;
    float: right;
    background: transparent;
    z-index: 4;
    cursor: pointer;
    &.active {
    	.mMt.one{
		    top: -50px;
		    opacity: 0;
		}
		.mMt.two{
		    transform: rotate(45deg);
		    top: 17px;
		}
		.mMt.three{
		    transform: rotate(-45deg);
		    top: 17px;
		}
		.mMt.four{
		    top: 50px;
		    opacity: 0;
		}
    }
    .mMt{
	    width: 26px;
	    height: 3px;
	    background: $text_pr;
	    display: block;
	    position: absolute;
	    transition: all .3s ease-in-out;
	    left: 5px;
	}
    .mMt.one {
	    top: 7px;
	}
	.mMt.two {
	    top: 13px;
	}
	.mMt.three {
	    top: 19px;
	}
	.mMt.four {
	    top: 25px;
	}
}

@media only screen and (max-width: 991px) {
	body {
		padding-top: 90px;
	}
	.breadcrumbs {
		&__itm {
			&:not(:last-child) {
				&:after {
					right: 5px;
				}
			}
			a {
				padding: 0 20px 0 0;
			}
		}
	}
	.a2a_counter {
		img {
		    margin: 6px 6px 0;
		}
	}
}

@media only screen and (max-width: 767px) {
	body {
	    padding-top: 120px;
	}
	h1 {
		font-size: 26px;
	}
}
