.main-content {
	padding: 45px 0 0;
	&__top {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	&__middle {
		@include row-flex();
		@include md(justify-content, center);
		align-items: baseline;
		padding: 30px 0 0;
	}
	&__sidebar {
		@include col();
		@include size(3);
		@include size-xl(3);
		@include size-lg(3);
		@include size-md(12);
		@include size-sm(12);
		@include size-xs(12);
		@include pageitem;
	}
	&__right {
		@include col();
		@include size(9);
		@include size-xl(9);
		@include size-lg(9);
		@include size-md(12);
		@include size-sm(12);
		@include size-xs(12);
		padding: 0 0 15px;
	}
	&__right-top {
		@include pageitem;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 0 0 13px;
	}
	&__right-bottom {
		@include pageitem;
		margin: 15px 0;
		padding: 20px 40px;
	}
	&__sidebar-itm {
		padding: 20px 23px 20px 20px;
		border-bottom: 2px solid $bdr;
		.dropdown {
			&__btn {
				justify-content: space-between;
			}
			&__content {
				border: none;
				background-color: transparent;
				position: static;
				padding: 20px 0 0;
			}
		}
		.chevron-down-ic {
			width: 12px;
			height: 10px;
			margin-right: -9px;
		}
		.check-set {
			display: flex;
			flex-wrap: wrap;
			&.w-50 {
				.cust-check {
					width: 50%;
				}
			}
			&.w-100 {
				.cust-check {
					width: 100%;
				}
			}
			.cust-check {
				margin: 10px 0;
			}
		}
		.range {
			&__wrap {
				margin: -20px 0 0;
			}
		}
	}
	&__show-more {
		overflow: hidden;
    	max-height: 160px;
    	@include trans;
    	&.show {
    		max-height: 1000px;
    	}
	}
}
.breadcrumbs {
	padding: 0 0 0 15px;
	&__itm {
		display: inline-block;
		list-style: none;
		position: relative;
		&:not(:last-child) {
			&:after {
				content: "";
				position: absolute;
				right: 14px;
				top: 5px;
				display: block;
				width: 8px;
				height: 10px;
				background-image: url("../img/svg/chevron-right.svg");
				background-size: cover;
			}
		}
		a, span {
			font-size: 13px;
			text-decoration: none;
		}
		a {
			@include links ($text_light, $text_light, $primary, $primary, $text_light);
			padding: 0 41px 0 0;
		}
	}
}

.add-to-any {
	display: flex;
	align-items: center;
	font-size: 13px;
	color: #b3b3b3;
}

.a2a_default_style {
	.a2a_count {
		background-color: transparent;
		border: 2px solid #f1e9e9;
		color: #b3b3b3;
		font-size: 13px !important;
		width: 35px !important;
		height: 30px !important;
		line-height: 28px !important;
	}
}

.a2a_counter {
	img {
		margin: 6px 20px 0;
	}
}

.page {
	&__heading {
		margin: 25px auto;
    	padding: 55px 0;
		text-align: center;
		position: relative;
		&:after {
			content: "";
			position: absolute;
			background-color: $primary;
			width: 68px;
			height: 2px;
			bottom: 2px;
			left: 50%;
			transform: translateX(-50%);
		}
	}
}

.irs--round {
	.irs-handle,
	.irs-bar,
	.irs-handle.state_hover,
	.irs-handle:hover {
		background-color: $primary;
	}
	.irs-min,
	.irs-max {
		display: none;
	}
	.irs-from,
	.irs-to {
		display: none;
		&:before {
			border-top-color: #000;
		}
		background-color: #000;
	}
	.irs-handle {
		width: 15px;
		height: 15px;
		top: 30px;
		cursor: pointer;
	}
}

.range {
	&__values {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 23px 0 8px;
	}
	&__value {
		border: 2px solid $bdr;
		border-radius: $radius;
		font-size: 12px;
		font-weight: 500;
		padding: 11px 10px 11px 25px;
		color: $text_pr;
		max-width: 88px;
	}
	&__value__wrap {
		position: relative;
		&:before {
			content: "$";
			position: absolute;
			left: 14px;
			top: 13px;
			font-size: 12px;
		}
	}
}

.show-more {
	display: block;
	position: relative;
	padding: 13px 20px 13px 40px;
	text-transform: uppercase;
	font-weight: 900;
	font-size: 10px;
	color: $text_sec;
	letter-spacing: 1.2px;
	text-align: left;
	opacity: .8;
	pointer-events: auto;
	cursor: pointer;
	@include trans;
	&:hover {
		opacity: 1;
	}
	&:before {
		content: "";
		position: absolute;
		width: 8px;
		height: 8px;
		background-image: url("../img/svg/cross-grey.svg");
		background-size: cover;
		left: 8px;
	}
	&.hide {
		opacity: 0;
		pointer-events: none;
	}
}

.content-filter {
	&__itm {
		display: inline-block;
		list-style: none;
		padding: 0 20px;
		&.active {
			.content-filter__lnk {
				color: $primary;
				&:before {
					width: 100%;
					left: 0;
				}
			}
		}
	}
	&__lnk {
		text-decoration: none;
		font-size: 13px;
		color: $lnk;
		padding: 23px 0;
		display: inline-block;
		position: relative;
		@include trans;
		&:before {
			content: "";
			position: absolute;
			bottom: 0;
			left: 50%;
			height: 2px;
			display: block;
			background-color: $primary;
			width: 0px;
			@include trans;
		}
		&:hover {
			color: $primary;
			&:before {
				width: 100%;
				left: 0;
			}
		}
	}
}


.grid-switch {
	display: flex;
	&__itm {
		display: flex;
    	justify-content: center;
    	flex-direction: column;
    	align-items: center;
		list-style: none;
		border-left: 2px solid $bdr;
		width: 60px;
		&:hover,
		&:active,
		&.active {
			i {
				&.grid-ic {
				background-image: url("../img/svg/grid-hover.svg");
				}
				&.list-ic {
					background-image: url("../img/svg/list-hover.svg");
				}
			}
		}
	}
	&__lnk {
		padding: 16px;
		cursor: pointer;
		i {
			@include trans;
		}
	}
}

.products-list {
	margin: 15px 0;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	&.style-columns {
		.prod-itm {
			width: 31%;
			flex-direction: column;
		}
	}
	&.style-rows {
		.prod-itm {
			width: 100%;
			display: grid;
			grid-template-columns: 20% 20% 20% 20% 20%;
			grid-template-rows: 15% 20% 20% 20% 25%;
			&__top {
				width: 100%;
				grid-column-start: 1;
				grid-column-end: 6;
			}
			&__a {
				grid-column-start: 1;
			    grid-column-end: 3;
			    grid-row-start: 2;
			    grid-row-end: 6;
			    margin: 10px 0;
			}
			&__name {
				grid-column-start: 3;
				grid-column-end: 6;
				grid-row-start: 2;
				grid-row-end: 4;
			}
			&__bottom {
				grid-column-start: 3;
			    grid-column-end: 6;
			    grid-row-start: 5;
			    grid-row-end: 5;
			}
			.rate__wrap {
				grid-column-start: 3;
				grid-column-end: 6;
				grid-row-start: 4;
				grid-row-end: 4;
			}
			.img-inner-box {
				max-width: 200px;
			}
		}
	}
	.prod-itm {
		@include pageitem;
		padding: 20px;
		display: flex;
		&__top {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
		&__top-links {
			list-style: none;
			li {
				display: inline-block;
				margin: 0 0 0 18px;
				a {
					&:hover {
						i {
							&.list-thin-ic {
								background-image: url("../img/svg/list-thin-hover.svg");
							}
							&.heart-light-ic {
								background-image: url("../img/svg/heart-light-hover.svg");
							}
						}
					}
				}
				i {
					height: 20px;
    				width: 22px;
    				@include trans;
				}
			}
		}
		&__a {
			margin: 20px 0 0;
			&:hover {
				.main-img {
					transform: translateX(-50%) scale(1.1);
				}
			}
			.main-img {
				transition: all 0.6s ease;
				transform: translateX(-50%) scale(1);
			}
		}
	}
}

.prod-itm {
	margin: 15px 0;
	&__name {
		color: $text_pr;
		text-decoration: none;
		text-align: center;
		font-size: 14px;
		display: inline-block;
		margin: 20px 0;
		line-height: 1.3em;
		&:hover {
			color: $primary;
		}
	}
	&__bottom {
		margin: 30px auto 15px;
		text-align: center;
	}
	&__cart {
		display: inline-block;
		margin: 0 12px;
		&:hover {
			i {
				background-image: url("../img/svg/cart2-hover.svg");
			}
		}
		i {
			@include trans;
		}
	}
}

.rate {
	&__wrap {
		display: flex;
		align-items: center;
    	justify-content: space-between;
    	width: 145px;
    	margin: 0 auto;
	}
	&__count {
		font-size: 12px;
		color: $text_sec;
		display: inline-block;
		margin: 0 0 -3px;
	}
}

.rate-base-layer,
.rate-hover-layer {
    cursor: pointer;
}

.price {
	font-size: 16px;
	font-weight: 900;
	color: $text_pr;
}

.pagination {
	display: flex;
	justify-content: space-between;
	align-items: center;
	a {
		text-decoration: none;
		text-transform: uppercase;
		color: $text_sec;
		font-weight: 900;
		font-size: 10px;
		letter-spacing: 1.3px;
		line-height: 20px;
		@include trans;
		i {
			@include trans;
		}
		&:hover {
			color: $primary;
			&.prev {
				i {
					background-image: url("../img/svg/arrow-left-hover.svg");
				}
			}
			&.next {
				i {
					background-image: url("../img/svg/arrow-right-hover.svg");
				}
			}
		}
		&.prev {
			i {
				margin: 0 20px 0 0;
			}
		}
		&.next {
			i {
				margin: 0 0 0 20px;
			}
		}
		.current-page {
			color: $text_pr;
			font-size: 14px;
		}
	}
}

@media only screen and (max-width: 991px) {
	.main-content {
		&__sidebar {
			position: fixed;
			padding: 50px 20px 0;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: 6;
			overflow: auto;
			opacity: 0;
			pointer-events: none;
			@include trans;
			&.active {
				pointer-events: auto;
				opacity: 1;
			}
			.close {
				position: absolute;
				top: 20px;
				left: 50%;
				transform: translateX(-50%);
			}
		}
		&__middle {

		}
	}
}

@media only screen and (max-width: 767px) {
	.main-content {
		&__top {
			flex-direction: column;
			.breadcrumbs {
				margin: 10px 0 15px;
			}
		}
	}
	.page {
		&__heading {
			padding: 20px 0;
		}
	}
	.products-list {
		&.style-columns {
			.prod-itm {
				width: 48%;
			}
		}
	}
	.content-filter {
		&__itm {
			padding: 0 10px;
		}
	}
}

@media only screen and (max-width: 576px) {
	.content-filter {
		&__lnk {
			font-size: 12px;
			padding: 20px 0;
		}
	}
	.grid-switch {
		&__lnk {
			padding: 5px;
		}
		&__itm {
			width: 40px;
		}
	}
}

@media only screen and (max-width: 480px) {
	.main-content {
		&__right-top {
			flex-direction: column;
			position: relative;
			.menu-burger {
				position: absolute;
				left: 10px;
				top: 65px;
			}
		}
		&__right-bottom {
			margin: 5px 0;
			padding: 15px;
		}
	}
	.content-filter {
		margin: 0 0 15px;
		&__itm {
		    padding: 0 5px;
		}
	}
	.grid-switch {
		&__itm {
			&:last-child {
				border-right: 2px solid $bdr;
			}
		}
	}
	.products-list {
		.prod-itm {
			padding: 10px;
		}
	}
	.prod-itm {
		margin: 10px 0;
		&__name {
			font-size: 12px;
		}
	}
	.pagination {
		a {
			&.prev {
				i {
				   margin: 0 5px 0 0;
				}
			}
			&.next {
				i {
					margin: 0 0 0 20px;
				}
			}
		}
	}
}

@media only screen and (max-width: 400px) {
	.products-list {
		&.style-columns {
			.prod-itm {
				width: 100%;
			}
		}
	}
}




@media only screen and (min-width: 992px) {
	.main-content {
		&__right-top {
			.menu-burger {
				display: none;
			}
		}
		&__sidebar {
			.close {
				display: none;
			}
		}
	}
}