@mixin trans {
	transition: all 0.3s ease;
}

@mixin links ($link, $visit, $hover, $active, $focus){
 	color: $link;
 	text-decoration: none;
 	outline: none;
 	&:visited {
 		color: $visit;
 		outline: none;
 	}
 	&:hover {
 		color: $hover;
 	}
    &:active {
 		color: $active;
 	}
 	&:focus {
 		color: $focus;
 	}
}

@mixin pageitem {
	background-color: $primary_bg;
	border-radius: $radius;
	box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.03);
}